.permissionModal table {
  width: 100%;
  margin-bottom: 20px; }
  .permissionModal table td {
    padding: 10px;
    text-align: center; }
    .permissionModal table td:first-child {
      text-align: left; }
    .permissionModal table td.check {
      padding: 0 10px; }
    .permissionModal table td label {
      padding: 10px 0;
      width: 100%;
      height: 100%; }
    .permissionModal table td label, .permissionModal table td input {
      cursor: pointer; }
  .permissionModal table select {
    max-width: 120px; }

.permissionModal label {
  font-weight: 300;
  position: relative;
  top: 1px;
  left: 3px; }
