.finder .files {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: relative;
  background-color: #fafafa;
  border-radius: 3px; }
  .finder .files .progress {
    width: 100%;
    position: absolute;
    height: 3px;
    top: 0;
    left: 0;
    display: none; }
  .finder .files .path .breadcrumb {
    margin: 0;
    background-color: #f0f0f0;
    border-radius: 3px 3px 0 0; }
    .finder .files .path .breadcrumb a {
      cursor: pointer; }
  .finder .files .files-container {
    min-height: 500px;
    padding: 0 10px 10px 0; }
    .finder .files .files-container:after {
      content: "";
      display: table;
      clear: both; }
    .finder .files .files-container .file {
      float: left;
      width: 120px;
      height: 156px;
      margin: 10px 0 0 10px; }
      .finder .files .files-container .file .thumb {
        position: relative; }
        .finder .files .files-container .file .thumb .icon-locked {
          position: absolute;
          width: 16px;
          height: 15px;
          right: 16px;
          bottom: 16px;
          color: #333;
          top: auto;
          font-size: 15px; }
      .finder .files .files-container .file.edited .thumb {
        background-color: #dff0d8; }
      .finder .files .files-container .file.selected .thumb {
        background-color: #e1e1e1; }
      .finder .files .files-container .file.selected .name span {
        color: #fff;
        background-color: #3875d7; }
      .finder .files .files-container .file.cut {
