.finder .btn.upload, .finder .btn.overwrite {
  position: relative; }
  .finder .btn.upload .lbl, .finder .btn.overwrite .lbl {
    font-weight: 300; }
  .finder .btn.upload input, .finder .btn.overwrite input {
